@import "/static/font-awesome/css/font-awesome.min.css";
* {
  padding: 0;
  margin: 0;
}
ul {
  list-style-type: none;
}
body {
  font-family: "微软雅黑";
}
a {
  color: #71777c;
  text-decoration: none;
}
a:hover {
  color: #007fff;
}
a.active {
  color: #007fff;
}
@font-face {
  font-family: 'FontAwesome';
  src: url('/static/font-awesome/fonts/fontawesome-webfont.eot?v=4.7.0');
  src: url('/static/font-awesome/fonts/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('/static/font-awesome/fonts/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('/static/font-awesome/fonts/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('/static/font-awesome/fonts/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('/static/font-awesome/fonts/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
body {
  background: #f4f5f5;
}
.common-nav {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #ffffff;
  border-bottom: 1px solid #f4f5f5;
}
.common-nav .nav-content {
  max-width: 960px;
  height: 100%;
  margin: 0 auto;
  display: flex;
}
.common-nav .nav-content .nav-logo {
  width: 120px;
  height: 100%;
}
.common-nav .nav-content .nav-list {
  width: 840px;
  height: 100%;
}
.common-nav .nav-content .nav-list ul li {
  display: inline-block;
  height: 100%;
  padding: 0 20px;
  line-height: 60px;
}
.common-nav .nav-content .nav-list ul li a {
  display: block;
  width: 100%;
  height: 100%;
}
